@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
:root {
  --skyscraper: #3189E6;
  --sudoku: #FD9A27; 
  --stela: #0C4473;
  --p2d: #94DDD9;
  --qstyle: rgb(181, 116, 150);
  --teapot: #de1d64;
  --thesis: #000;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
   font-family: 'Open Sans', sans-serif;
   font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.titlebar-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid-container {
  width: 100%;
}
.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	padding-top: 20px;
	padding-bottom: 20px;
	align-items: center;
	position: fixed;
	background-color: white;
	z-index: 10;
	top: 0;
}

.titlebar-left {
	display: flex;
	flex-grow: 1;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
	font-size: 18px;
	font-weight: 400px;
}

.nav-item:hover {
	color: black;
}

.titlebar-center {
	flex-grow: 2;
	height: 100%;
}

.titlebar-right {
	flex-grow: 1;
	display: flex;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
	height: 100%;
	max-width: 45px;
	cursor: pointer;
}

.hamburger:hover {
	opacity: 0.3;
}

.hamburger-menu {
	top: 0;
	position: fixed;
	padding-top: 10px;
	padding-bottom: 15px;
	height: 80px;
	width: 100%;
	background-color: #aaa;
	z-index: 10;

	-webkit-transition: height 1s ease;
  -webkit-transition: ease-in  2s none;
  transition: ease-in  2s none; 
}

.hamburger-menu__text {
	padding-top: 5px;
	padding-bottom: 5px;
	width: 100%;
	color: white;
	font-weight: 600;
}

.hamburger-menu__text:active, .hamburger-menu__text:hover {
	opacity: 0.7;
}

@media only screen and (min-width: 710px) {
	.hamburger-menu {
		display: none;
	}
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar-center {
		flex-grow: 1;
	}

	.titlebar-right {
		justify-content: center;
	}

	.hamburger {
		display: block;
	}

	.byline {
		display: none;
	}

	.hamburger-menu {
		display: block;
	}
}

.link {
	text-decoration: none;
	color: inherit;
}

.grid {
	width: 100%;
	margin-top: 100px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-flow: row wrap;
}

.grid-item {
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% / 3); 
	height: calc(100vw / 3);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.grid-link {
	text-decoration: none;
	display: flex;
	flex-grow: 1;
	flex-shrink: 0;
	flex-basis: calc(100% / 3); 
	height: calc(100vw / 3);
}

.grid-img {
	width: 60%;
	height: 200px;
	max-width: 200px;
	max-height: 200px;
	min-width: 100px;
	min-height: 100px;

	margin-right: 10px;
	margin-left: 10px;
	margin-top: 10px;
}

.grid-text {
	visibility: hidden;
	margin-top: 20px;
	font-weight: 700;
	font-size: 16px;
	z-index: 1;
}

.grid-item:hover {
	opacity: 0.7;
	cursor: pointer;
}

.grid-item:hover .grid-text {
	visibility: visible;
	display: block;
	color: white;
}

.skyscraper {
	background-color: var(--skyscraper);
}

.sudoku {
	background-color: var(--sudoku);
}

.stela {
	background-color: var(--stela);
}

.p2d {
	background-color: var(--p2d);
}

.qstyle {
	background-color: var(--qstyle);
}

.teapot {
	background-color: var(--teapot);
}

.thesis {
	background-color: var(--thesis);
}

@media only screen and (max-width: 700px) {
	.grid {
		flex-direction: column;
	}

	.grid-item {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-link {
		flex: 0 1 auto;
		width: 100vw;
		height: 100vw;
	}

	.grid-item:active .grid-text {
		visibility: visible;
		display: block;
		color: white;
	}

	.p2d:active {
		background-color: rgba(150, 221, 217, 0.7);
	}

	.skyscraper:active {
		background-color: rgba(55, 139, 247, 0.7);
	}

	.sudoku:active {
		background-color: rgba(251, 153, 56, 0.7);
	}

	.stela:active {
		background-color: rgba(16, 69, 113, 0.7);
	}
} 
.portfolio-container {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
	position: relative;
}

.portfolio-img:hover {
	-webkit-filter: brightness(80%);
	        filter: brightness(80%);
	cursor: pointer;
}

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
  justify-content: center;
  text-align: center;
}

.image-container {
	display: flex;
	flex-direction: column;
	float: left;
	margin-top: 100px;
}

.portfolio-img {
	height: calc(100vh - 140px);
	background-color: black;
}

.text-container {
	width: 18vw;
	height: 100%;
	min-width: 150px;
	position: absolute;
	display: flex;
	align-items: flex-end;
	text-align: left;
	margin-left: calc(((100vh - 120px) / 2) + 10vw);
	font-family: 'Roboto';
}

.dot-container {
	width: 100%;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.dot {
  height: 10px;
  width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin-left: 2px;
  margin-right: 2px;
}

.dot:hover {
	background-color: #333;
	cursor: pointer;
}

.selected {
	background-color: #333;
}

@media only screen and (max-width: 700px) {
	.portfolio-img {
		width: 100%;
		height: inherit;
	}

	.portfolio-container {
		position: static;
		flex-direction: column;
	}

	.text-container {
		display: inline-block;
		width: 90%;
		margin-left: 0;
		text-align: center;
		position: static;
	}

	.portfolio-img:hover {
		-webkit-filter: brightness(100%);
		        filter: brightness(100%);
		cursor: pointer;
	}
}
