@import '../../index.css';

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	padding-top: 20px;
	padding-bottom: 20px;
	align-items: center;
	position: fixed;
	background-color: white;
	z-index: 10;
	top: 0;
}

.titlebar-left {
	display: flex;
	flex-grow: 1;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
	color: #aaa;
	font-size: 18px;
	font-weight: 400px;
}

.nav-item:hover {
	color: black;
}

.titlebar-center {
	flex-grow: 2;
	height: 100%;
}

.titlebar-right {
	flex-grow: 1;
	display: flex;
	justify-content: flex-end;
}

.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

.hamburger {
	display: none;
	height: 100%;
	max-width: 45px;
	cursor: pointer;
}

.hamburger:hover {
	opacity: 0.3;
}

.hamburger-menu {
	top: 0;
	position: fixed;
	padding-top: 10px;
	padding-bottom: 15px;
	height: 80px;
	width: 100%;
	background-color: #aaa;
	z-index: 10;

	-webkit-transition: height 1s ease;
  -moz-transition: ease-in 2s none;
  -ms-transition: ease-in  2s none;
  -o-transition: ease-in  2s none;
  transition: ease-in  2s none; 
}

.hamburger-menu__text {
	padding-top: 5px;
	padding-bottom: 5px;
	width: 100%;
	color: white;
	font-weight: 600;
}

.hamburger-menu__text:active, .hamburger-menu__text:hover {
	opacity: 0.7;
}

@media only screen and (min-width: 710px) {
	.hamburger-menu {
		display: none;
	}
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .nav-item {
		display: none;
	}

	.titlebar-center {
		flex-grow: 1;
	}

	.titlebar-right {
		justify-content: center;
	}

	.hamburger {
		display: block;
	}

	.byline {
		display: none;
	}

	.hamburger-menu {
		display: block;
	}
}

.link {
	text-decoration: none;
	color: inherit;
}
