.portfolio-container {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
	position: relative;
}

.portfolio-img:hover {
	filter: brightness(80%);
	cursor: pointer;
}

.titlebar-container {
	width: 100%;
	display: flex;
	align-items: center;
  justify-content: center;
  text-align: center;
}

.image-container {
	display: flex;
	flex-direction: column;
	float: left;
	margin-top: 100px;
}

.portfolio-img {
	height: calc(100vh - 140px);
	background-color: black;
}

.text-container {
	width: 18vw;
	height: 100%;
	min-width: 150px;
	position: absolute;
	display: flex;
	align-items: flex-end;
	text-align: left;
	margin-left: calc(((100vh - 120px) / 2) + 10vw);
	font-family: 'Roboto';
}

.dot-container {
	width: 100%;
	height: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.dot {
  height: 10px;
  width: 10px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin-left: 2px;
  margin-right: 2px;
}

.dot:hover {
	background-color: #333;
	cursor: pointer;
}

.selected {
	background-color: #333;
}

@media only screen and (max-width: 700px) {
	.portfolio-img {
		width: 100%;
		height: inherit;
	}

	.portfolio-container {
		position: static;
		flex-direction: column;
	}

	.text-container {
		display: inline-block;
		width: 90%;
		margin-left: 0;
		text-align: center;
		position: static;
	}

	.portfolio-img:hover {
		filter: brightness(100%);
		cursor: pointer;
	}
}